<template>
    <div id="container" />
</template>

<script>
import { Column } from "@antv/g2plot";

export default {
    name: "index",
    props: {
        data: {
            type: Array,
            default: () => {
                return [];
            },
        },
    },
    data() {
        return { list: [] };
    },
    mounted() {
        let that = this;
        const stackedColumnPlot = new Column("container", {
            data: that.data,
            isGroup: true,
            xField: "",
            yField: "",
            seriesField: "name",
            /**  */
            //color: ['#1ca9e6', '#f88c24'],
            /**  */
            // marginRatio: 0.1,
            label: {
                //  label 
                position: "middle", // 'top', 'middle', 'bottom'
                // 
                layout: [
                    // 
                    { type: "interval-adjust-position" },
                    // 
                    { type: "interval-hide-overlap" },
                    // 
                    { type: "adjust-color" },
                ],
            },
        });
        stackedColumnPlot.render();
    },
};
</script>

<style></style>
