import { defineComponent, onMounted, reactive, ref } from "vue";
import { Sankey } from "@antv/g2plot";
import "./CSS/index.css";

export default defineComponent({
    name: "SankeyGenerate",
    setup() {
        let name = ref("airhang");
        let data = reactive([
            { source: "", target: " UV", value: 160 },
            { source: "", target: " UV", value: 40 },
            { source: "", target: " UV", value: 10 },
            { source: "", target: " UV", value: 10 },
            { source: "", target: " UV", value: 8 },
            { source: "", target: " UV", value: 27 },
            { source: " UV", target: "", value: 30 },
            { source: " UV", target: "", value: 40 },
            { source: " UV", target: "", value: 35 },
            { source: " UV", target: "", value: 25 },
            { source: " UV", target: "", value: 10 },
            { source: " UV", target: "", value: 30 },
            { source: " UV", target: "", value: 40 },
            { source: " UV", target: "", value: 45 },
        ]);
        onMounted(() => {
            const sankey = new Sankey("container6", {
                data,
                sourceField: "source",
                targetField: "target",
                weightField: "value",
                nodeWidthRatio: 0.008,
                nodePaddingRatio: 0.03,
            });

            sankey.render();
        });
        return {
            name,
        };
    },
    render() {
        return (
            <div id="container6" class="sanKey animate__animated animate__fadeIn animate__faster animate__zoomIn">
                {this.name}
            </div>
        );
    },
});
